* {
        box-sizing: border-box;
        padding: 0;
        margin: 0;
        scroll-behavior: smooth;
}

body{
        /* ff 3.6+ */
        background:-moz-radial-gradient(circle at 3% 25%, rgba(255, 255, 255, 1) 0%, rgba(196, 196, 196, 1) 100%);

        /* safari 5.1+,chrome 10+ */
        background:-webkit-radial-gradient(circle at 3% 25%, rgba(255, 255, 255, 1) 0%, rgba(196, 196, 196, 1) 100%);
    
        /* opera 11.10+ */
        background:-o-radial-gradient(circle at 3% 25%, rgba(255, 255, 255, 1) 0%, rgba(196, 196, 196, 1) 100%);
    
        /* ie 10+ */
        background:-ms-radial-gradient(circle at 3% 25%, rgba(255, 255, 255, 1) 0%, rgba(196, 196, 196, 1) 100%);
    
        /* global 92%+ browsers support */
        background:radial-gradient(circle at 3% 25%, rgba(255, 255, 255, 1) 0%, rgba(196, 196, 196, 1) 100%);
}

a {
color: unset;
text-decoration: none;
}

.gradient__bg {

/* ff 3.6+ */
background:-moz-radial-gradient(circle at 3% 25%, rgba(255, 255, 255, 1) 0%, rgba(196, 196, 196, 1) 100%);

/* safari 5.1+,chrome 10+ */
background:-webkit-radial-gradient(circle at 3% 25%, rgba(255, 255, 255, 1) 0%, rgba(196, 196, 196, 1) 100%);

/* opera 11.10+ */
background:-o-radial-gradient(circle at 3% 25%, rgba(255, 255, 255, 1) 0%, rgba(196, 196, 196, 1) 100%);

/* ie 10+ */
background:-ms-radial-gradient(circle at 3% 25%, rgba(255, 255, 255, 1) 0%, rgba(196, 196, 196, 1) 100%);

/* global 92%+ browsers support */
background:radial-gradient(circle at 3% 25%, rgba(255, 255, 255, 1) 0%, rgba(196, 196, 196, 1) 100%);
}

.gradient__text {
background: var(--gradient-text);
background-clip: text;
-webkit-background-clop: text;
-webkit-text-fill-color:transparent;
}

.section__padding {
padding: 4rem 6rem;
}

.section__margin {
margin: 4rem 6rem;
}

  /*For Different sized Devices*/
  @media screen and (max-width: 700px){
        .section__padding {
            padding: 4rem;
        }
        .section__margin {
          margin: 4rem;
      }
    }
  
    @media screen and (max-width: 550px){
      .section__padding {
          padding: 4rem 2rem;
      }
      .section__margin {
        margin: 4rem 1rem;
    }
  }
    